<template>
  <div class="wrapper">
    <div v-if="showPage_flag">
      <!--地址页面-->
      <div class="aui-padded-15 white-bg aui-border-b">
        <div v-if="address">
          <div class="aui-font-size-16">
            <span>收货人：{{address.rec_name}}</span>
            <span class="aui-pull-right">{{address.rec_tel}}</span>
          </div>
          <div class="aui-margin-t-10 aui-padded-b-15">
            <span class="aui-font-size-14">收货地址：{{address.detail}}</span>
          </div>
        </div>
        <div v-else>
          <div class="aui-margin-b-10 aui-text-center">
            <span class="aui-font-size-14 text-grey">暂无默认地址，请进行设置</span>
          </div>
        </div>
        <div class="aui-text-center aui-padded-t-15 aui-border-t">
          <span class="aui-font-size-14 aui-text-primary" @click="clickChangeAddr()">点击设置地址</span>
        </div>
      </div>

      <!--商品信息及数量选择页面-->
      <div class="aui-margin-t-15">
        <div class="aui-padded-15 aui-font-size-14 white-bg">
          <!--头像部分-->
          <div class="aui-flex-col aui-flex-middle aui-border-b aui-padded-b-10">
            <img :src="goods.editor.avatar" style="border-radius: 50%;" class="tab-icon-22"/>
            <span class="aui-margin-l-10 text-oneline">{{goods.editor.nick_name}}</span>
          </div>
          <!--商品部分-->
          <div class="aui-row aui-margin-t-10">
            <div class="aui-col-xs-3">
              <img :src="goods.img + '?imageView2/1/w/300/h/300/interlace/1'"/>
            </div>
            <div class="aui-col-xs-9">
              <div>
                <span class="aui-font-size-14 aui-margin-l-10 text-twoline" style="line-height: 24px;">
                  {{goods.name}}
                </span>
              </div>
              <div class="aui-margin-t-10">
              <span class="aui-margin-l-10 aui-text-danger">
                ￥ {{goods.price}}
              </span>
                <span class="aui-text-default aui-pull-right aui-margin-r-10">
                x {{goods_num}}
              </span>
              </div>
            </div>
          </div>
          <!--商品说明-->
          <div class="aui-row aui-margin-t-15">
          <span class="aui-font-size-14 text-grey">
          此商品不支持7天无理由退还
          </span>
          </div>
          <!--数量调整-->
          <div class="aui-row aui-margin-t-15 aui-margin-b-15 aui-border-t aui-padded-t-15">
            <div class="aui-col-xs-6">
              <span class="aui-font-size-14">购买数量</span>
            </div>
            <div class="aui-col-xs-6 aui-text-right">
            <span class="aui-font-size-14 grey-bg" style="height: 32px;line-height: 32px;padding: 10px 12px;"
                  @click="clickMinusNum()">
              -
            </span>
              <span class="aui-margin-l-15 aui-margin-r-15">
              {{goods_num}}
            </span>
              <span class="aui-font-size-14 grey-bg" style="height: 32px;line-height: 32px;padding: 10px 12px;"
                    @click="clickAddNum();">
              +
            </span>
            </div>
          </div>
          <!--输入备注-->
          <div>
            <textarea class="textarea aui-font-size-14" v-model="remark" placeholder="订单备注..." rows=""
                      cols="">{{ remark }}</textarea>
          </div>
        </div>
      </div>
    </div>
    <!--底部tab-->
    <footer class="aui-bar aui-bar-tab" id="footer" style="border-top: 1px solid #f1f1f1;">
      <div class="aui-row" style="height: 50px;">
        <div class="aui-col-xs-2">
          <div class="aui-padded-t-15">
            <img src="../../../../static/img/go_back_icon_b.png" style="width: 20px;height: 20px;margin: auto;"
                 @click="clickHome()"/>
          </div>
        </div>
        <div class="aui-col-xs-6">
          <div>
            <span class="aui-font-size-16 aui-pull-right aui-margin-r-15 aui-margin-t-15">
              <span class="text-grey">合计</span> <span
              class="aui-text-danger">{{(goods.price * goods_num).toFixed(2)}}元</span>
            </span>
          </div>
        </div>
        <div class="aui-col-xs-4">
          <div class="aui-font-size-16" style="height: 50px;background: #dd191b;"
               @click="clickPay()">
            <div style="line-height: 50px;" class="aui-text-white aui-font-size-16">立即购买</div>
          </div>
        </div>

      </div>

    </footer>
  </div>
</template>

<script>

  // 微信jssdk
  import wx from 'weixin-js-sdk'

  import {Toast} from 'mint-ui';

  var goods_id = null;      //页面传入的goods_id

  var self = null;    //在create方法中初始化为this

  /*
   * 商品详情页面，需要传入goods_id，即商品id
   *
   * By TerryQi
   *
   * 2018-10-27
   *
   */
  export default {
    components: {},
    data() {
      return {
        showPage_flag: false,   //展示页面的标识
        goods: {"price": 0},      //商品信息
        address: null,     //用户地址
        remark: "",    //下单备注
        goods_num: 1,      //商品数量
      }
    },
    created() {
      self = this;    //使用self来代替this，避免this无效
      //此处有一个大坑！！！！！！！！！！！！
      let config = {};
      config.url = window.location.href;
      self.common.consoledebug.log("config:" + JSON.stringify(config));
      self.common.consoledebug.log("window.location.hash:" + JSON.stringify(window.location.hash));
      // 判断当前url是否存在?参数匹配符
      if (!config.url.match(/\?/)) {
        location.replace(window.location.href.split('#')[0] + '?' + window.location.hash);
        return;
      }
    },
    mounted() {
      goods_id = this.$route.params.goods_id;
      console.log("goods_id:" + goods_id);
      this.init();   //初始化页面
    },
    methods: {
      //初始化页面
      init: function () {
        //获取商品信息
        self.api.goods_getById({id: goods_id, level: "01234"}).then((res) => {
          // self.common.consoledebug.log("res:" + JSON.stringify(res));
          if (res.data.result) {
            this.goods = res.data.ret;
            this.showPage_flag = true;
          }
        }).catch((err) => {
          self.common.consoledebug.log("err:" + JSON.stringify(err));
          Toast(JSON.stringify(err.data.message));
        })
        //获取微信配置信息
        var wxConfig_param = {
          "url": window.location.href.split('#')[0],
          "jsApiList": "chooseWXPay,openAddress"
        }
        this.api.wechat_wxConfig(wxConfig_param).then((res) => {
          // self.common.consoledebug.log("wechat_wxConfig res:" + JSON.stringify(res));
          if (res.data.result) {
            var wxConfig = res.data.ret;
            wx.config(wxConfig);
          }
        }).catch((err) => {

        });
        //获取默认地址信息
        this.getDefaultAddress();
      },
      //获取地址
      getDefaultAddress: function () {
        self.api.address_getDefault({}).then((res) => {
          // self.common.consoledebug.log("getDefaultAddress res:" + JSON.stringify(res));
          if (res.data.result) {
            this.address = res.data.ret;
          }
        }).catch((err) => {
          self.common.consoledebug.log("err:" + JSON.stringify(err));
          Toast(JSON.stringify(err.data.message));
        })
      },
      //点击返回首页
      clickHome: function () {
        self.common.goBack({router: this.$router});
      },
      //点击客服
      clickCustomService: function () {
        self.common.jumpToCustomService();
      },
      //点击转发
      clickTrans: function () {

      },
      //添加数量
      clickAddNum: function () {
        if (this.goods_num >= 9) {
          Toast("单笔订单最多购买9组商品");
          return;
        }
        this.goods_num = this.goods_num + 1;    //商品数加1
      },
      //减少数量
      clickMinusNum: function () {
        if (this.goods_num <= 1) {
          return;
        }
        this.goods_num = this.goods_num - 1;    //商品数减1
      },
      //点击更换地址
      clickChangeAddr: function () {
        //通过微信获取默认地址
        wx.openAddress({
          success: function (res) {
            var param = {
              "rec_name": res.userName,
              "zip_code": res.postalCode,
              "province": res.provinceName,
              "city": res.cityName,
              "detail": res.detailInfo,
              "rec_tel": res.telNumber
            }
            //保存地址
            self.api.address_edit(param).then((res) => {
              if (res.data.result) {
                //保存成功后重新获取地址信息
                self.api.getDefaultAddress();
              }
            }).catch((err) => {
              self.common.consoledebug.log("err:" + JSON.stringify(err));
              Toast(JSON.stringify(err.data.message));
            })
          }
        });
      },
      //点击购买
      clickPay: function () {
        self.common.consoledebug.log("clickPay");
        //首先进行地址校验
        if (self.address == null || self.common.judgeIsNullStr(self.address.rec_name) || self.common.judgeIsNullStr(self.address.rec_tel) || self.common.judgeIsNullStr(self.address.detail)) {
          Toast("请选择地址");
          return false;
        }
        //进行参数封装
        var param = {
          "goods_id": self.goods.id,
          "goods_num": self.goods_num,
          "rec_name": self.address.rec_name,
          "rec_tel": self.address.rec_tel,
          "rec_address": self.address.detail
        }
        // alert("param:" + JSON.stringify(param))
        //进行支付
        self.api.order_payOrder(param).then((res) => {
          self.common.consoledebug.log("order_payOrder res:" + JSON.stringify(res));
          if (res.data.result) {
            var wxPay_config = res.data.ret;
            self.common.consoledebug.log("order_payOrder wxPay_config:" + JSON.stringify(wxPay_config));
            wx.chooseWXPay({
              timestamp: wxPay_config.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              nonceStr: wxPay_config.nonceStr, // 支付签名随机串，不长于 32 位
              package: wxPay_config.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
              signType: wxPay_config.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              paySign: wxPay_config.paySign, // 支付签名
              success: function (res) {
                // 支付成功后的回调函数
                self.common.jumpToPaySuccess();
              }
            });
          }
        }).catch((err) => {

        });
      }
    }
  }
</script>

<style scoped>


</style>



